.footer {
  background-color: $basic-1;
  padding: 90px 0;
  &__block {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
  &__social {
    display: flex;
    justify-content: space-between;
    flex-direction: row;
    max-width: 190px;
    width: 100%;
    margin-bottom: 50px;
    img {
      margin: 0 5px;
    }
  }
  &__nav {
    margin-bottom: 50px;
    max-width: 740px;
    width: 100%;
    .nav-list {
      display: flex;
      justify-content: space-between;
      .nav-item {
        list-style: none;
        padding-right: 20px;
        margin-right: 20px;
        border-right: 2px solid $basic-2;
        &:last-child {
          border: none;
          padding: 0;
        }
        .nav-link {
          text-decoration: none;
          font-family: $roboto;
          font-style: normal;
          font-weight: 500;
          font-size: 24px;
          line-height: 28px;
          color: $txt-light;
        }
      }
    }
  }
  &__info {
    @include content;
    color: #ffffff;
  }
}
