.section__form {
  margin-bottom: 100px;
  .form {
    &__title {
      text-align: center;
      margin: 0 auto;
      max-width: 1035px;
      width: 100%;
      @include h2;
      margin-bottom: 60px;
      span {
        color: $basic-2;
      }
    }
    &__btn {
      width: 100%;
      height: 70px;
    }
  }
  form {
    margin: 0 auto;
    max-width: 530px;
    width: 100%;
    input,
    textarea,
    select {
      background: #fff7f7;
      border: 2px solid $basic-1;
      box-sizing: border-box;
      box-shadow: 0px 3px 6px #c6cfcf;
      width: 100%;
      height: 70px;
      margin-bottom: 30px;
      padding-left: 30px;
      padding-right: 30px;
      @include content;
      color: $gray;
      &::placeholder {
        @include content;
        color: $gray;
      }
      option {
        @include content;
        color: $gray;
      }
    }
    textarea {
      margin-bottom: 50px;
      height: 180px;
      padding-top: 20px;
    }
  }
}
