.section__blog {
  margin-top: 50px;
  margin-bottom: 100px;
  .blog {
    &__title {
      @include h3;
      text-align: center;
      margin-bottom: 60px;
    }
    &__block {
      display: flex;
      flex-direction: row;
      justify-content: space-between;
      flex-wrap: wrap;
    }
    &__box {
      max-width: 420px;
      width: 100%;
      min-height: 590px;
      margin-bottom: 40px;
      background: #ffffff;
      box-shadow: 0px 3px 4px rgba(186, 197, 196, 0.5);
      img {
        width: 100%;
        max-height: 300px;
        margin-bottom: 10px;
      }
    }
    &__text {
      padding: 0 20px;
      .date {
        margin-bottom: 20px;
        @include content-2;
        color: $gray;
      }
      .article {
        &__title {
          font-family: $roboto;
          font-style: normal;
          font-weight: 500;
          font-size: 24px;
          line-height: 28px;
          color: $basic-1;
          text-decoration: none;
        }
        &__text {
          margin-top: 10px;
          @include content-2;
          color: $txt-dark;
        }
      }
    }
    &__pagination {
      display: flex;
      align-items: center;
      justify-content: center;
      a {
        font-family: $exo;
        font-style: normal;
        font-weight: 500;
        font-size: 40px;
        line-height: 48px;
        color: $basic-1;
        text-decoration: none;
        margin: 0 5px;
      }
      .pagination__btn {
        width: 40px;
        height: 40px;
        background-color: $basic-2;
        box-shadow: 0px 2px 4px #adbbbb;

        display: flex;
        align-items: center;
        justify-content: center;
        margin: 0 30px;
      }
      .prev {
        //transform: rotate(0deg);
      }
      .next {
      }
    }
  }
}
