.section__category {
  margin-top: 50px;
  .category {
    &__title {
      @include h3;
      text-align: center;
      margin-bottom: 60px;
      span {
        color: $basic-2;
      }
    }
    &__block {
      margin-bottom: 100px;
      justify-content: space-between;
      flex-direction: row;
      flex-wrap: wrap;
      display: flex;
    }
    &__box {
      max-width: 310px;
      width: 100%;
      background-color: #fff;
    }
    &__img {
      img {
        width: 100%;
        max-height: 300px;
      }
    }
    &__name {
      background-color: $basic-1;
      display: flex;
      justify-content: center;
      align-items: center;
      width: 100%;
      height: 70px;
      a {
        text-decoration: none;
        @include content;
        color: $txt-light;
        text-align: center;
      }
    }
  }
}
.section__catalog {
  margin-top: 50px;
  margin-bottom: 100px;
  h2 {
    @include h3;
    text-align: center;
    margin-bottom: 60px;
    color: $basic-1;
  }
  .catalog {
    &__btn {
      max-width: 406px;
      width: 100%;
      height: 60px;
      margin: 10px auto 0;
    }
  }
}
.catalog {
  &__block {
    justify-content: space-between;
    flex-direction: row;
    flex-wrap: wrap;
    display: flex;
  }
  &__box {
    background: #ffffff;
    box-shadow: 0px 3px 4px rgba(155, 151, 151, 0.31);
    height: 480px;
    max-width: 310px;
    width: 100%;
    margin-bottom: 40px;
    padding: 0 20px;
    .price {
      display: flex;
      justify-content: center;
      flex-direction: row;
      p {
        @include h5;
        color: $basic-1;
      }
      .sale {
        margin-right: 10px;
      }
      .nosale {
        text-decoration-color: #ff0000;
        text-decoration-line: line-through;
      }
    }
  }
  &__img {
    img {
      width: 100%;
      height: 236px;
      margin-bottom: 10px;
    }
  }
  &__title {
    display: flex;
    justify-content: center;
    a {
      @include h5;
      text-align: center;
      color: $basic-1;
      margin-bottom: 20px;
      text-decoration: none;
    }
  }
  &__specification {
    display: flex;
    justify-content: center;
    p {
      text-align: center;
      @include content-2;
      margin-bottom: 35px;
    }
  }
}
