.section__about {
  .container {
    display: flex;
    justify-content: space-between;
    flex-direction: row;
  }
  .about {
    &__block {
      img {
        width: 100%;
      }
    }
    &__img {
      max-width: 530px;
      width: 100%;
      display: flex;
      align-items: end;
    }
    &__box {
      margin-top: 100px;
      max-width: 630px;
      width: 100%;
      margin-right: 10px;
    }
    &__title {
      font-style: normal;
      font-weight: bold;
      font-size: 38px;
      line-height: 46px;
      margin-bottom: 40px;
    }
    &__text {
      @include content;
      margin-bottom: 20px;
    }
  }
}
