//пролистывание брендов в автоматическом режиме
.section__brands {
  margin-bottom: 100px;
  .brands {
    &__box {
      height: 70px;

      display: flex;
      justify-content: center;
      text-align: center;
      img {
        max-width: 100%;
        height: 70px;
      }
    }
  }
  /* Слайдер */
  .slick-slider {
    min-width: 0;
  }
  /* Ограничивающая оболочка */
  .slick-list {
    overflow: hidden;
  }
  /* Лента слайдов */
  .slick-track {
    display: flex;
  }
}

//Блок отзывов
.section__feedback {
  margin-bottom: 100px;

  .feedback {
    &__title {
      @include h2;
      text-align: center;
      margin-bottom: 60px;
      span {
        color: $basic-2;
      }
    }

    &__text {
      @include content;
      margin-bottom: 34px;
      text-overflow: ellipsis;
    }
    &__people {
      font-family: $exo;
      font-style: normal;
      font-weight: 500;
      font-size: 24px;
      line-height: 29px;
      color: $gray;
    }
  }
  /* Слайдер */
  .slick-slider {
    min-width: 0;
    position: relative;
  }
  /* Ограничивающая оболочка */
  .slick-list {
    overflow: hidden;
    padding: 0;
  }
  /* Лента слайдов */
  .slick-track {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    width: 100%;
  }
  /* Слайд */
  .slick-slide {
    background: #ffffff;
    box-shadow: 0px 2px 4px 2px #d2dbdb;
    padding: 40px;
    max-width: 380px;
    width: 100%;
    //height: 340px;
    height: auto;
  }

  /* Слайд по центру */
  .slick-slide.slick-center {
    max-width: 460px;
    width: 100%;
    //height: 390px;
    height: auto;
    margin-top: 40px;
  }

  /* Стрелка */
  .slick-arrow {
    width: 60px;
    height: 60px;
    background: $basic-2;
    box-shadow: 0px 2px 4px #adbbbb;
    position: absolute;
    top: 35%;
    border: none;
  }
  /* Стрелка влево */
  .slick-arrow.slick-prev {
    left: -20px;
    z-index: 100;
  }
  /* Стрелка вправо */
  .slick-arrow.slick-next {
    right: -20px;
    z-index: 100;
  }
}

.product__photo {
  max-width: 550px;
  width: 100%;
  .slider {
    &-for {
      /* Слайдер */
      .slick-slider {
        min-width: 0%;
      }
      /* Ограничивающая оболочка */
      .slick-list {
        overflow: hidden;
        padding: 0;
      }
      /* Лента слайдов */
      .slick-track {
        display: flex;
        justify-content: space-between;
        align-items: flex-start;
        width: 100%;
      }
      /* Слайд */
      .slick-slide {
        width: 100%;
        margin-bottom: 10px;
        img {
          width: 100%;
        }
      }
    }
    &-nav {
      /* Слайдер */
      .slick-slider {
        min-width: 0;
      }
      /* Ограничивающая оболочка */
      .slick-list {
        overflow: hidden;
        padding: 0;
      }
      /* Лента слайдов */
      .slick-track {
        display: flex;
        justify-content: space-between;
      }

      /* Слайд */
      .slick-slide {
        max-width: 129px;
        width: 100%;
        height: 129px;
        cursor: pointer;
        img {
          width: 100%;
        }
      }

      /* Слайд по центру */
      .slick-slide.slick-center {
        border: 2px solid $basic-1;
        box-sizing: border-box;
      }
    }
  }
}
