.section__numbers {
  background-color: $basic-1;
  margin-bottom: 100px;
  .numbers {
    &__block {
      display: flex;
      flex-direction: row;
      justify-content: space-between;
      align-items: center;
      min-height: 250px;
    }
    &__box {
      text-align: center;
      h3 {
        @include h1;
        color: $basic-2;
        margin-bottom: 10px;
      }
      h4 {
        @include h3;
        color: $txt-light;
      }
    }
  }
}
