.section__service {
  margin-bottom: 100px;
  margin-top: 50px;
  .service {
    &__title {
      @include h2;
      text-align: center;
      margin-bottom: 60px;
      span {
        color: $basic-2;
      }
    }
    &__block {
      display: flex;
      justify-content: space-between;
      flex-direction: row;
    }
    &__tabs {
      display: flex;
      flex-direction: column;
      max-width: 350px;
      width: 100%;
      //float: left;
      .service__btn {
        border: none;
        cursor: pointer;
        display: flex;
        align-items: center;
        text-align: center;
        padding: 17px 16px;
        max-width: 350px;
        width: 100%;
        min-height: 100px;
        background-color: $basic-1;
        margin-bottom: 20px;
        font-family: $roboto;
        font-style: normal;
        font-weight: 500;
        font-size: 28px;
        line-height: 33px;
        color: $txt-light;
        text-decoration: none;
      }
    }
    &__content {
      max-width: 900px;
      width: 100%;
      h3 {
        @include h3;
        text-align: center;
        color: $basic-1;
        margin-bottom: 40px;
      }
      p {
        @include content;
        margin-bottom: 40px;
      }
    }
  }
  .hidden {
    display: none;
  }
}
