.section__stages {
  background-color: $basic-1;
  padding: 80px 0 120px;
  margin-bottom: 100px;
  .stages {
    // &__full {
    //   background-image: url(../img/home/stages.webp);
    //   background-repeat: no-repeat;
    //   background-position: right;
    // }
    &__title {
      @include h2;
      text-align: center;
      color: $txt-light;
      margin-bottom: 60px;
      span {
        color: $basic-2;
      }
    }
    &__block {
      display: flex;
      flex-direction: row;
      justify-content: space-between;
    }
    &__img {
      max-width: 1060px;
      width: 100%;
      height: auto;
      img {
        width: 100%;
      }
    }
    &__text {
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      max-width: 395px;
      width: 100%;
      //height: 650px;
      margin-right: 155px;
      h3 {
        @include h1;
        color: $basic-2;
        margin-right: 20px;
      }
      p {
        @include content;
        color: $txt-light;
      }
    }
    &__box {
      display: flex;
      flex-direction: row;
      align-items: center;
    }
  }
}
