.section__product {
  margin-top: 50px;
  .container {
    display: flex;
    justify-content: space-between;
    flex-direction: row;
  }
  .product {
    &__text {
      max-width: 708px;
      width: 100%;
      .price {
        display: flex;
        justify-content: left;
        flex-direction: row;
        p {
          @include h4;
          color: $txt-dark;
        }
        .sale {
          margin-right: 10px;
        }
        .nosale {
          text-decoration-color: #ff0000;
          text-decoration-line: line-through;
        }
      }
      .star {
        max-width: 300px;
        width: 100%;
        @include content-2;
        color: $gray;
        margin-bottom: 30px;
      }
    }
    &__title {
      @include h4;
      color: $basic-1;
      margin-bottom: 30px;
    }
    &__specification {
      @include content-2;
      margin-bottom: 36px;
    }
    &__btn {
      width: 220px;
      height: 60px;
    }
  }
}
