.section__advantages {
  margin-bottom: 32px;
  .container {
    background-image: url(../img/bg/advantages__bg.webp);
    background-position: center;
    background-repeat: no-repeat;
  }
  .around {
    margin: 117px 70px;
  }
  .advantages {
    &__title {
      text-align: center;
      @include h2;
      margin-bottom: -57px;
      span {
        color: $basic-2;
      }
    }
    &__block {
      display: flex;
      flex-direction: row;
      justify-content: space-between;
      align-items: center;
      .box-left {
        text-align: left;
        h3 {
          position: relative;
          &::after {
            content: "";
            position: absolute;
            left: 0;
            display: block;
            width: 50px;
            border-bottom: 2px solid $basic-2;
          }
        }
      }
      .box-right {
        text-align: right;
        h3 {
          position: relative;
          &::after {
            content: "";
            position: absolute;
            right: 0;
            display: block;
            width: 50px;
            border-bottom: 2px solid $basic-2;
          }
        }
      }
    }

    &__box {
      max-width: 335px;
      width: 100%;

      h3 {
        //font-family: $roboto;
        font-style: normal;
        font-weight: 500;
        font-size: 32px;
        line-height: 37px;
        margin-bottom: 10px;
      }
      p {
        @include content;
      }
    }
  }
}
