.section__pluses {
  background-color: $basic-1;
  padding: 70px 0 60px;
  margin-bottom: 100px;
  background-image: url(../img/home/pluses__people.webp);
  background-repeat: no-repeat;
  background-position: center bottom;
  .container {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
  }
  .pluses {
    &__title {
      @include h2;
      text-align: center;
      color: $txt-light;
      margin-bottom: 128px;
      span {
        color: $basic-2;
      }
    }
    &__block {
      max-width: 400px;
      width: 100%;
    }
    &__box {
      max-width: 400px;
      width: 100%;
      margin-bottom: 40px;
      h3 {
        position: relative;
        font-family: $roboto;
        font-style: normal;
        font-weight: 500;
        font-size: 32px;
        line-height: 37px;
        color: $txt-light;
        margin-bottom: 10px;
        &::after {
          content: "";
          position: absolute;
          left: 0;
          display: block;
          width: 50px;
          border-bottom: 2px solid $basic-2;
        }
      }
      p {
        @include content;
        color: $txt-light;
      }
    }
  }
}
