.section__portfolio {
  margin-bottom: 100px;
  margin-top: 50px;
  .portfolio {
    &__title {
      @include h3;
      text-align: center;
      margin-bottom: 60px;
      span {
        color: $basic-2;
      }
    }
    &__block {
      display: flex;
      flex-direction: row;
      flex-wrap: wrap;
      justify-content: space-between;
    }
    &__box {
      max-width: 406px;
      width: 100%;
      max-height: 555px;
      height: 100%;
      margin-bottom: 41px;
      position: relative;
      transition: 2s;

      img {
        max-width: 406px;
        width: 100%;
        max-height: 555px;
        height: 100%;
      }
      &:hover .portfolio__text {
        display: block;
        position: absolute;
        width: 100%;
        height: 100%;
        top: 0;
        display: flex;
        align-items: center;
        background: rgba(77, 118, 117, 0.86);
        transition: 1s;
      }
    }
    &__text {
      display: none;

      p {
        @include h4;
        color: $txt-light;
        text-align: center;
      }
    }
    &__btn {
      margin: 10px auto 100px;
      max-width: 406px;
      width: 100%;
      height: 60px;
    }
  }
}
