.section__contacts {
  margin-bottom: 100px;
  margin-top: 50px;
  .container {
    position: relative;
  }
  .contacts {
    &__title {
      text-align: center;
      @include h3;
      margin-bottom: 60px;
    }
    &__block {
      padding: 67px 0;
      z-index: 100;
    }

    &__maps {
      max-width: 856px;
      width: 100%;
      min-height: 665px;
      position: absolute;
      right: 0;
      top: 0;
      z-index: -100;
      iframe {
        width: 100%;
        min-height: 665px;
      }
    }
    &__box {
      background-color: $basic-1;
      max-width: 550px;
      width: 100%;
      height: 150px;
      margin-bottom: 40px;
      display: flex;
      justify-content: center;
      flex-direction: column;
      text-align: center;
      p {
        margin-bottom: 20px;
        @include content-2;
        color: $bg;
      }
      a {
        @include content;
        color: $txt-light;
      }
    }
  }
}
