.section__single-post {
  margin: 50px 0 100px;
  .post__title {
    @include h3;
    color: $basic-1;
    text-align: center;
    margin-bottom: 60px;
  }
  img {
    width: 100%;
    height: auto;
    margin-bottom: 40px;
  }
  .content {
    p {
      max-width: 1080px;
      width: 100%;
      @include content;
      margin-bottom: 20px;
    }
    h3 {
      max-width: 1080px;
      width: 100%;
      @include h4;
      margin-top: 50px;
      margin-bottom: 20px;
    }
  }
}
