header {
  padding: 20px 0;
  box-shadow: 0px 3px 4px rgba(155, 151, 151, 0.31);
}

.navbar {
  display: flex;
  justify-content: space-between;
  flex-direction: row;
  align-items: center;

  nav {
    max-width: 580px;
    width: 100%;
    margin: 0 auto;
  }
  .nav {
    &-list {
      display: flex;
      justify-content: space-between;
      flex-direction: row;
    }
    &-item {
      list-style: none;
    }
    &-link {
      text-decoration: none;
      font-style: normal;
      font-weight: 500;
      font-size: 24px;
      line-height: 28px;
      color: $txt-dark;
    }
    &-phone {
      a {
        text-decoration: none;
        font-style: normal;
        font-weight: 500;
        font-size: 30px;
        line-height: 35px;
        color: $txt-dark;
      }
    }
    &-brand {
      mix-blend-mode: darken;
    }
  }
}

.head {
  background-color: $basic-1;
  background-image: url(../img/bg/head__bg.webp);
  background-repeat: no-repeat;
  background-position: center;
  padding: 225px 0;
  margin-bottom: 100px;
  &__block {
    max-width: 750px;
    width: 100%;
    .title {
      @include h1;
      color: $txt-light;
      margin-bottom: 60px;
    }
  }
  .head__button {
    display: flex;
  }
  .head__btn {
    width: 220px;
    height: 60px;
    font-style: normal;
    font-weight: 500;
    font-size: 24px;
    line-height: 28px;
    border: 2px solid #fecc47;
    box-sizing: border-box;
    display: flex;
    justify-content: center;
    align-items: center;
    color: $txt-light;
    text-decoration: none;
    margin-right: 20px;
  }
  .btn {
    color: $txt-dark;
  }
}
