// базовые настройки
.container {
  max-width: 1300px;
  width: 100%;
  margin: 0 auto;
}

body {
  background: $bg;
  color: $txt-dark;
  font-family: $roboto;
}
.btn {
  font-style: normal;
  font-weight: 500;
  font-size: 24px;
  line-height: 28px;
  border: 2px solid #fecc47;
  box-sizing: border-box;
  display: flex;
  justify-content: center;
  align-items: center;
  text-decoration: none;
  color: $txt-dark;
  background-color: $basic-2;
}
h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: $exo;
}

@import "_media.scss";
